import {ModuleWithProviders, NgModule, Optional, SkipSelf} from '@angular/core';
import {CommonModule} from '@angular/common';
import {HttpClient, HttpClientModule} from '@angular/common/http';
import {RouteReuseStrategy, RouterModule} from '@angular/router';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';

import {RouteReusableStrategy} from './route/route-reusable-strategy';
import {I18nService} from './i18n/i18n.service';
import {HttpService} from './http/http.service';
import {HttpCacheService} from './http/http-cache.service';
import {ApiPrefixInterceptor} from './http/api-prefix.interceptor';
import {ErrorHandlerInterceptor} from './http/error-handler.interceptor';
import {CacheInterceptor} from './http/cache.interceptor';
import {JwtInterceptor, LOGIN_URL, PUBLIC_URL_PATTERNS} from './http/jwt.interceptor';
import {TranslateHttpLoader} from "@ngx-translate/http-loader";
import {CookieService} from "ngx-cookie-service";
import {CsrfInterceptor} from "./http/csrf.interceptor";
import {MockInterceptor} from "./http/mock.interceptor";

export const I18N_FOLDER_PATH: string = './assets/i18n/';


@NgModule({
  imports: [
    CommonModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (HttpLoaderFactory),
        deps: [HttpClient, I18N_FOLDER_PATH]
      }
    }),
    RouterModule
  ],
  declarations: [],
  providers: [
    I18nService,
    HttpCacheService,
    ApiPrefixInterceptor,
    ErrorHandlerInterceptor,
    CacheInterceptor,
    CookieService,
    JwtInterceptor,
    CsrfInterceptor,
    MockInterceptor,
    {
      provide: HttpClient,
      useClass: HttpService
    },
    {
      provide: RouteReuseStrategy,
      useClass: RouteReusableStrategy
    }
  ]
})
export class DccCommonsNgServicesModule {
  constructor(@Optional() @SkipSelf() parentModule: DccCommonsNgServicesModule) {
    // Import guard
    if (parentModule) {
      throw new Error(`${parentModule} has already been loaded. Import Services module in the AppModule only.`);
    }
  }

  static forRoot(@Optional() publicUrlPatterns: Array<string> = [], @Optional() loginUrl: string, @Optional() i18nFolderPath: string): ModuleWithProviders {
    return {
      ngModule: DccCommonsNgServicesModule,
      providers: [
        {provide: PUBLIC_URL_PATTERNS, useValue: publicUrlPatterns},
        {provide: LOGIN_URL, useValue: loginUrl},
        {provide: I18N_FOLDER_PATH, useValue: i18nFolderPath}
      ]
    }
  }

}

export function HttpLoaderFactory(http: HttpClient, i18nFolderPath: string) {
  return new TranslateHttpLoader(http, i18nFolderPath, '.json');
}
