import {Injectable} from '@angular/core';
import {
  HttpEvent,
  HttpHandler,
  HttpHeaders,
  HttpInterceptor,
  HttpRequest,
  HttpXsrfTokenExtractor
} from '@angular/common/http';
import {Observable} from 'rxjs';
import {CookieService} from "ngx-cookie-service";
import {Router} from "@angular/router";

const CSRF_HEADER_NAME = 'X-XSRF-TOKEN';

@Injectable()
export class CsrfInterceptor implements HttpInterceptor {


  constructor(private cookieService: CookieService, private router: Router, private tokenExtractor: HttpXsrfTokenExtractor) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    const token = this.tokenExtractor.getToken() as string;

    // add authorization header with jwt token if available
    let headers: HttpHeaders = request.headers;
    if (token != null && request.headers.get(CSRF_HEADER_NAME) == null && request.method == 'POST') {
      headers = request.headers.append(CSRF_HEADER_NAME, token);
    }
    request = request.clone({headers});
    return next.handle(request);
  }

}
